import React from "react"
import logo from './newlogo.png';
import './Nav.css';
function Nav() {
  return (
    <nav>
    <div id="tot">
      <div>
      <img id="log" src={logo} alt="Logo" />
      </div>
      <h1 id="newhead">Newton School</h1>
    </div>
    </nav>
  )
}

export default Nav